html{
    scroll-behavior: smooth;
}

@media (max-width: 768px) {
    .card_container {
        flex-direction: column;
    }
}

.card-img-top {
    width: 10rem;
    height: 10rem;
    margin: auto;
    margin-top: 1rem;
    border: 2px solid red;
    border-radius: 50%;
    overflow: visible;
    padding:.8rem .8rem;
}
