.btn-myDanger{
    background-color: red;
    padding: 0.5rem;
    color: white;
    width: 8rem;
    margin: auto;
    margin-bottom: 1rem;
    border-top-left-radius: 1rem;
    border-bottom-left-radius: 1rem;
}
.btn-myDanger:hover{
    background-color: rgb(221, 11, 11);
    color: white;
}
.btn-myDanger:active{
    background-color: rgb(221, 11, 11);
    color: white;
}

/* Searchbar styling */
.search-btn{
    width: 50px;
    height: 46px;
    font-size: 1.8rem;
    background: #E14D2A;
    color: #fff;
    border: none;
    text-align: center;
    border-top-right-radius: 1rem;
    border-bottom-right-radius: 1rem;
    /* cursor: pointer; */
}
